        //     this.isPrivate,
        //     this.form.profileImgSrc
        // );
        //
        // //todo:     
        // if (result) {
        //     this.$router.push(`/community/${this.communityId}/setting/channel`);
        // }
    }

    validateState(name) {
        const { $dirty, $error } = this.$v.form[name]!;
        return $dirty ? !$error : null;
    }

    getProfileImgSrc(imgSrc: string) {
        this.form.profileImgSrc = imgSrc;
    }
}
