import Vue from 'vue'
import VueRouter, {RouteConfig} from 'vue-router'
import {i18n} from "@/plugins/i18n";
import routes from './router'
import {SUPPORTED_LOCALES} from '../common/locale';
import store from "@/store";
import {forEach} from "lodash";
Vue.use(VueRouter)


// duplicate error
const originalPush = VueRouter.prototype.push;
VueRouter.prototype.push = function push(location) {
    //@ts-ignore
    return originalPush.call(this, location).catch(err => {
        if (err.name !== 'NavigationDuplicated') throw err;
    });
};

function getLocaleRegex() {
    let reg = ''
    SUPPORTED_LOCALES.forEach((locale, index) => {
        reg = `${reg}${locale.code}${index !== SUPPORTED_LOCALES.length - 1 ? '|' : ''}`
    })
    return `(${reg})`
}

const router = new VueRouter({
    scrollBehavior: () => ({x: 0, y: 0}),
    //@ts-ignore
    mode: process.env.VUE_APP_ROUTER_MODE,
    base: process.env.VUE_ROUTER_BASE,
    routes: [
        {
          path:'/',
          redirect:`/${i18n.locale}`
        },
        /**
         *  
         */
        // {
        //     name: 'redirect',
        //     path: '/redirect*',
        //     component: () => import( '@/views/Redirect.vue' ),
        // },
        {
        path: `/:locale${getLocaleRegex()}?`,
        component: {
            template: '<router-view></router-view>'
        },
        children: routes
    }]
});

//title 
router.afterEach((to, from) => {
    // i18n.locale = to.params.lang;

    Vue.nextTick(() => {
        document.title = to.meta!.title;
    });
});

router.beforeEach((to, from, next) => {
    console.log('to',to)
    // This goes through the matched routes from last to first, finding the closest route with a title.
    // e.g., if we have `/some/deep/nested/route` and `/some`, `/deep`, and `/nested` have titles,
    // `/nested`'s will be chosen.
    const nearestWithTitle = to.matched.slice().reverse().find(r => r.meta && r.meta.title);

    // Find the nearest route element with meta tags.
    const nearestWithMeta = to.matched.slice().reverse().find(r => r.meta && r.meta.metaTags);

    const previousNearestWithMeta = from.matched.slice().reverse().find(r => r.meta && r.meta.metaTags);

    // If a route with a title was found, set the document (page) title to that value.
    if(nearestWithTitle) {
        document.title = nearestWithTitle.meta.title;
    } else if(previousNearestWithMeta) {
        document.title = previousNearestWithMeta.meta.title;
    }

    // Remove any stale meta tags from the document using the key attribute we set below.
    Array.from(document.querySelectorAll('[data-vue-router-controlled]')).map(el => el.parentNode?.removeChild(el));

    Array.from(document.querySelectorAll('[rel]')).map(el => el.parentNode?.removeChild(el));

    // Skip rendering meta tags if there are none.
    if(!nearestWithMeta) return next();
    // Turn the meta tag definitions into actual elements in the head.


    const link1 = document.createElement('link');

    link1.setAttribute('rel', 'alternate');
    link1.setAttribute('hreflang', i18n.locale);
    link1.setAttribute('href', `${store.getters.homeUrl}${to.path}`);

    document.head.appendChild(link1)



    forEach(to.meta?.metaTags, tag=>{
        if(tag.canonical){
            const link2 = document.createElement('link');

            link2.setAttribute('rel', 'canonical');
            link2.setAttribute('href', `${store.getters.homeUrl}/${tag.canonical}`);

            document.head.appendChild(link2)
        }
    })


    nearestWithMeta.meta.metaTags.map(tagDef => {
        const tag = document.createElement('meta');


        Object.keys(tagDef).forEach(key => {
            tag.setAttribute(key, tagDef[key]);
        });

        // We use this to track which meta tags we create so we don't interfere with other ones.
        tag.setAttribute('data-vue-router-controlled', '');

        return tag;
    })
        // Add the meta tags to the document head.
        .forEach(tag => document.head.appendChild(tag));

    next();
});

export default router
