
import moment from "moment";

function dateFormat(sec: number) {
    if (moment().diff(moment(sec), 'm') < 1 && moment().diff(moment(sec), 's') >= 0) {
        return moment().diff(moment(sec), 's') + ' '
    } else if (moment().diff(moment(sec), 'h') < 1) {
        return moment().diff(moment(sec), 'm') + ' '
    } else if (moment().diff(moment(sec), 'd') < 1) {
        return moment().diff(moment(sec), 'h') + ' '
    } else if (moment().diff(moment(sec), 'd') <= 7) {
        return moment().diff(moment(sec), 'd') + ' '
    } else {
        return moment(sec).locale('ko').format('LL')
    }

}

export {
    dateFormat
}